import React from 'react';

function Botox() {
  return (
    <div className="procedures-wrapper">
      <h1 className="procedure-title">BOTOX Cosmetic</h1>
<p className="procedure-content">BOTOX Cosmetic is a non-surgical procedure which injects botulinum toxin into the skin. This is done in order to eliminate forehead creases and frown lines. The toxin rejuvenates affects the skin, making it look smoother, by blocking nerve impulses and paralyzing muscles which cause wrinkling. BOTOX Cosmetic is also documented as an effective method of alleviating migraine headaches.</p>
    </div>
  );
}

export default Botox;



