import React from 'react';

function WhatIsUPSS() {
  return (
    <div>
      <p className="faqs-title">What is the Utah Plastic Surgery Society?</p>
      <div className="faqs-content">
        <p>The Utah State Plastic Surgery Society is comprised of professionals that have been certified by the <a href="https://www.abplasticsurgery.org/" className="link">American Board of Plastic Surgery</a>, the only plastic surgery board recognized by the <a href="http://www.abms.org/">American Board of Medical Specialties</a>. To become certified by the <a href="https://www.abplasticsurgery.org/" className="link">American Board of Plastic Surgery</a> our affiliate surgeons have completed at least five years of residency training after earning his or her medical degree. During this residency training program, candidates are regularly evaluated, and their work continues to be reviewed even after the completion of the program. Only the most knowledgeable and skilled plastic surgeons are certified by the board, which means that only the most knowledgeable and skilled cosmetic and reconstructive surgeons in the state of Utah are listed in our directory.</p><br />
        <p>Before undergoing cosmetic or reconstructive surgery, you must first choose the plastic surgeons most qualified to perform your procedure(s). Our board certified plastic surgeons represent the most highly esteemed practitioners in the field of cosmetic surgery. Each one of our affiliate plastic surgeons has completed at least five years of rigorous residency training and is certified by the American Board of Plastic Surgery. Before a board certification is granted, a prospective candidate must pass rigorous oral and written examinations by demonstrating exceptional knowledge and skill. Each of the cosmetic and reconstructive surgeons in the Utah State Plastic Surgery Society directory is trained and proficient in performing all types of cosmetic and reconstructive plastic surgery. Prospective patients can trust that our surgeons have met the most rigorous academic and professional requirements, in addition to demonstrating outstanding surgical skill throughout their careers.</p><br />
        <p>We cannot stress the importance of choosing only the most knowledgeable and experienced plastic surgeons for all of your reconstructive and cosmetic surgery procedures, a process made easy with the Utah State Plastic Surgery Society (USPSS) directory, which lists only the most reputable and highly successful plastic surgeons who practice in the state of Utah.</p><br />
        <p>Please take the time to review our site, read about the various reconstructive and cosmetic surgery procedures which may interest you, and use the directory to contact our board certified plastic surgeons.</p><br />
      </div>
    </div>
  );
}

export default WhatIsUPSS;



